export default function ModuleExtSweetAlert(toolkit) {
    toolkit.showError = (message) => sweetAlert('Oops...', message, 'error')

    toolkit.isFine = (res, success = 'success', message = 'message') => {
        if (!res[success]) {
            sweetAlert('Oops...', res[message], 'error')
            return false
        }

        return true
    }
}
