export default function ModuleArrayObject(toolkit) {
    toolkit.isLastElement = (arr, item) => ((arr.indexOf(item) + 1) === arr.length)

    toolkit.return = (...args) => {
        if (args.length === 0) {
            return null
        }

        if (args.length === 1) {
            return args[0]
        }

        return args
    }
    toolkit.distinct = (arr) => arr.filter((item, i, self) => self.indexOf(item) === i)

    toolkit.merge = (...args) => {
        if (args.length === 0) {
            return null
        }

        if (args.length === 1) {
            return args[0]
        }

        let first = args[0]

        args.forEach((d) => {
            if (toolkit.isObject(d)) {
                first = Object.assign({}, first, d)
            } else if (toolkit.isArray(d)) {
                first = first.concat(d)
            }
        })

        return first
    }

    toolkit.makeObject = (...args) => {
        if (args.length % 2 != 0) {
            args.push('')
        }

        let o = {};
        for (let i = 0; i < args.length; i += 2) {
            o[args[i]] = args[i+1]
        }

        return o
    }

    toolkit.clone = (o) => {
        if (toolkit.isArray(o)) {
            return o.slice(0)
        }

        return toolkit.merge({}, o)
    }

    toolkit.allKeys = (obj) => {
        let keys = []
        for (let key in obj) {
            if (obj.hasOwnProperty(key)) {
                keys.push(String(key))
            }
        }
        return keys
    }

    toolkit.length = (o) => {
        if (toolkit.isObject(o)) {
            return toolkit.allKeys(o).length
        }

        return o.length
    }

    toolkit.forEach = (o, callback) => {
        if (toolkit.isObject(o)) {
            for (let key in o) {
                if (o.hasOwnProperty(key)) {
                    callback(key, o[key])
                }
            }

            return
        }

        o.forEach((d, i) => {
            callback(d, i)
        })
    }

    toolkit.split = (arr, separator = '', length = 0) => {
        if (length < 0) {
            length = 0
        }

        if (length === 0) {
            return arr.split(separator)
        }

        return arr.split(separator).filter((_, i) => {
            return i < length
        })
    }
}
