export default function ModuleExtTooltipster(toolkit) {
    toolkit.prepareTooltipster = (o = '.tooltipster', argConfig = toolkit.noob) => {
        let $tooltipster = toolkit.$(o)

        $tooltipster.each((i, e) => {
            let position = 'top'

            if (toolkit.$(e).attr('class').search('tooltipster-') > -1) {
                position = toolkit.$(e).attr('class')
                    .split(' ')
                    .find((d) => d.search('tooltipster-') > -1)
                    .replace(/tooltipster\-/g, '')
            }

            let config = {
                theme: 'tooltipster-val',
                animation: 'grow',
                delay: 0,
                offsetY: -5,
                touchDevices: false,
                trigger: 'hover',
                position: position,
                content: toolkit.newEl('div').html(toolkit.$(e).attr('title'))
            }

            config = toolkit.merge(config, argConfig)

            toolkit.$(e).tooltipster(config)
        })
    }

    toolkit.gridBoundTooltipster = (o) => () => toolkit.prepareTooltipster(toolkit.$(o).find(".tooltipster"))
}
