import ModuleTypeCast from './type-cast.js'
import ModuleArrayObject from './array-object.js'
import ModuleConsole from './console.js'
import ModuleString from './string.js'
import ModuleUtil from './util.js'
import ModuleMath from './math.js'
import ModuleURLHTTP from './url-http.js'
import ModuleExtJQuery from './ext/jquery.js'
import ModuleExtKendoUI from './ext/kendo-ui.js'
import ModuleExtTooltipster from './ext/tooltipster.js'

import ModuleExtSweetAlert from './ext/sweetalert.js'
import ModuleExtKnockout from './ext/knockout.js'

export default function Toolkit() {
    let toolkit = {}

    toolkit.dev = true
    toolkit.noop = () => {}
    toolkit.noob = {}

    ModuleTypeCast(toolkit)
    ModuleArrayObject(toolkit)
    ModuleConsole(toolkit)
    ModuleString(toolkit)
    ModuleUtil(toolkit)
    ModuleMath(toolkit)
    ModuleURLHTTP(toolkit)

    if (typeof window !== 'undefined') {
	    if (typeof jQuery !== 'undefined') {
	        ModuleExtJQuery(toolkit)
	    }

	    if (typeof jQuery !== 'undefined') if (jQuery.fn.hasOwnProperty('kendoGrid')) {
	        ModuleExtKendoUI(toolkit)
	    }

	    if (typeof jQuery !== 'undefined') if (jQuery.fn.hasOwnProperty('tooltipster')) {
	        ModuleExtTooltipster(toolkit)
	    }

	    if (typeof sweetAlert !== 'undefined') {
	        ModuleExtSweetAlert(toolkit)
	    }

	    if (typeof ko !== 'undefined') {
	        ModuleExtKnockout(toolkit)
	    }
    }

    return toolkit
}

;(() => {
	if (typeof module !== 'undefined' && typeof module.exports !== 'undefined') {
		module.exports = Toolkit()
	}

	if (typeof define === 'function' && define.amd) {
		define([], () => Toolkit())
	}

	if (typeof window !== 'undefined') {
		if (!window.hasOwnProperty('jQuery')) {
		    console.warn('WARNING: toolkit.js required jQuery. Please include the library.')
		    return
		}

		window.toolkit = Toolkit()
	}
})()