export default function ModuleMath(toolkit) {
    toolkit.safeDiv = (numberX, numberY) => toolkit.number(numberX / numberY)

    toolkit.valueInXPercent = (number, percent = 100) => number * percent / 100
    toolkit.increaseXPercent = (number, percent = 0) => number + toolkit.valueInXPercent(number, percent)
    toolkit.decreaseXPercent = (number, percent = 0) => number - toolkit.valueInXPercent(number, percent)
    toolkit.percentOfXOnY = (numberX, numberY) => numberX / numberY * 100
    toolkit.growthOfXOnY = (numberX, numberY) => (numberY - numberX) / numberX * 100

    toolkit.randomNumber = (min = 0, max = 9) => (Math.floor(Math.random() * (max - min + 1)) + min)

    toolkit.hardRound = (number, what = 'round') => {
        let num = toolkit.toInt(number)
        let base = String(num).length - 1
        let divider = Math.pow(10, String(Math.floor(num)).length - 1)
        let value = Math[what](num / divider) * Math.pow(10, base)

        return value
    }
    toolkit.hardCeil = (number) => toolkit.hardRound(number, 'ceil')
    toolkit.hardFloor = (number) => toolkit.hardRound(number, 'floor')

    toolkit.sum = (arr, callback = null) => {
        return arr.reduce((m, x) => {
            let f = x

            if (toolkit.isFunction(callback)) {
                f = callback(x)
            } else if (toolkit.isString(callback)) {
                f = x[callback]
            }

            if (!toolkit.isNumber(f)) {
                f = 0
            }

            return m + f
        }, 0)
    }

    toolkit.avg = (arr, callback = null) => {
        return toolkit.sum(arr, callback) / arr.length
    }
}
