export default function ModuleString(toolkit) {
    toolkit.trim = (o) => o.replace(/(^\s+|\s+$)/g, '')

    toolkit.capitalize = (o) => {
        if (toolkit.isEmptyString(o, true)) {
            return ''
        }

        return o.toLowerCase().split(' ')
            .map((d) => (d.length > 0) ? (d[0].toUpperCase() + d.slice(1)) : 0)
            .join(' ')
    }

    toolkit.replace = (o, chars, replacement) => {
        if (toolkit.isString(chars)) {
            chars = [chars]
        }

        chars.forEach((d) => {
            o = o.replace(new RegExp(`\\${d}`, 'g'), replacement)
        })

        return o
    }
}
