export default function ModuleUtil(toolkit) {
    // alphanum sort by Brian Huisman
    // based on the Alphanum Algorithm by David Koelle
    // http://www.davekoelle.com/files/alphanum.js

    toolkit.alphaNumSort = (arr, isCaseSensitive = true) => {
        for (let z = 0, t; t = arr[z]; z++) {
            let x = 0, y = -1, n = 0, i, j
            arr[z] = [];

            while (i = (j = t.charAt(x++)).charCodeAt(0)) {
                let m = (i === 46 || (i >= 48 && i <= 57))
                if (m !== n) {
                    arr[z][++y] = ''
                    n = m
                }
                
                arr[z][y] += j;
            }
        }

        arr.sort((a, b) => {
            for (let x = 0, aa, bb; (aa = a[x]) && (bb = b[x]); x++) {
                if (!isCaseSensitive) {
                    aa = aa.toLowerCase()
                    bb = bb.toLowerCase()
                }

                if (aa !== bb) {
                    let c = Number(aa), d = Number(bb)
                    if (c === aa && d === bb) {
                        return c - d;
                    } else {
                        return (aa > bb) ? 1 : -1
                    }
                }
            }

            return a.length - b.length
        });

        for (let z = 0; z < arr.length; z++) {
            arr[z] = arr[z].join('')
        }

        return arr
    }

    toolkit.try = (callbackTry = toolkit.noop) => toolkit.tryCatchFinally(callbackTry)
    toolkit.tryCatch = (callbackTry = toolkit.noop, callbackCatch = toolkit.noop) => toolkit.tryCatchFinally(callbackTry, c2)
    toolkit.tryCatchFinally = (callbackTry = toolkit.noop, callbackCatch = toolkit.noop, callbackFinally = toolkit.noop) => {
        try {
            toolkit.call(callbackTry)
        } catch (err) {
            toolkit.call(callbackCatch, err)
        } finally {
            toolkit.call(callbackFinally)
        }
    }

    toolkit.call = (callback, args) => {
        if (toolkit.isString(callback)) {
            callback = eval(callback)
        }

        if (args.length > 0) {
            return callback.apply(null, args)
        }

        return callback()
    }

    toolkit.callAfter = (callback, delay = 300) => {
        setTimeout(() => {
            toolkit.call(callback)
        }, delay)
    }

    toolkit.repeat = (n, callback = toolkit.noop) => {
        let res = []

        for (let i = 0; i < n; i++) {
            if (toolkit.isFunction(callback)) {
                res.push(toolkit.call(callback, i))
            } else {
                res.push(callback)
            }
        }

        return res
    }

    toolkit.callXTimes = (callback, n = 1, delay = 300) => {
        let counter = 0
        let interval = setInterval(() => {
            toolkit.call(callback, counter++)

            if (--n <= 0) {
                clearInterval(interval)
            }
        }, delay)

        return interval
    }

    toolkit.callUntil = (callback, until = null, delay = 300) => {
        if (until === null) {
            toolkit.callXTimes(callback, 1, delay)
            return 
        }

        let counter = 0
        let interval = setInterval(() => {
            toolkit.call(callback, counter++)

            if (until(counter)) {
                clearInterval(interval)
            }
        }, delay)

        return interval
    }

    toolkit.getNumberFromString = (o) => parseFloat(toolkit.string(o).replace(/[^\d\.]*/g, ''))
}
