export default function ModuleURLHTTP(toolkit) {
    toolkit.ajaxPost = (url, ...args) => {
        let data = {}
        let ajaxConfig = {}
        let callbackSuccess = toolkit.noop
        let callbackError = toolkit.noop

        if (toolkit.isFunction(toolkit.whenUndefined(args[0], toolkit.noop))) {
            callbackSuccess = args[0]
            callbackError = toolkit.whenUndefined(args[1], toolkit.noop)
        } else if (toolkit.isFunction(toolkit.whenUndefined(args[1], toolkit.noop))) {
            data = args[0]
            callbackSuccess = args[1]
            callbackError = toolkit.whenUndefined(args[2], toolkit.noop)
        } else {
            data = args[0]
            ajaxConfig = args[1]
            callbackSuccess = args[2]
            callbackError = toolkit.whenUndefined(args[3], toolkit.noop)
        }

        let param = data
        if (toolkit.isDefined(window.ko)) {
            param = ko.mapping.toJSON(data)
        }

        let config = {
            url: url.toLowerCase(),
            type: 'post',
            dataType: 'json',
            contentType: 'application/json charset=utf-8',
            data: param,
            success: (a) => {
                callbackSuccess(a)
            },
            error: (a, b, c) => {
                callbackError(a, b, c)
            }
        }

        if (data instanceof FormData) {
            delete config.config
            config.data = data
            config.async = false
            config.cache = false
            config.contentType = false
            config.processData = false
        }

        config = toolkit.merge(config, ajaxConfig)
        return jQuery.ajax(config)
    }

    toolkit.parseQueryString = (o) => {
        return (o || document.location.search)
            .replace(/(^\?)/,'')
            .split('&')
            .map(function (n) {
                n = n.split('=')
                this[n[0]] = n[1]
                return this
            }.bind(this))[0]
    }
}
