export default function ModuleExtKendoUI(toolkit) {
    toolkit.resetValidation = (selector) => {
        let target = toolkit.$(selector)

        let $form = $(target).data('kendoValidator')
        if (!$form) {
            $form = $(target).kendoValidator().data('kendoValidator')
        }

        toolkit.try(() => {
            $form.hideMessages()
        })
    }

    toolkit.isFormValid = (selector) => {
        let target = toolkit.$(selector)

        toolkit.resetValidation(target)
        return $(selector).data('kendoValidator').validate()
    }

    toolkit.fixKendoMultiSelect = () => {
        // Ainur works
        
        $('*.k-multiselect').change(function (e) {
            var existingValue = $(this).attr('existingValue')
            if (existingValue !== undefined) {
                var d = $(this).find(`select[multiple="multiple"]`).data('kendoMultiSelect')
                var newValue = existingValue.split(',')
                var existingDataSource = d.dataSource._data

                if (d.value().length > 0) {
                    newValue.push(d.value()[0])
                    d.setDataSource(existingDataSource)
                }
            }
        })

        $('*.k-multiselect').keydown(function (e) {
            var d = $(this).find(`select[multiple="multiple"]`).data('kendoMultiSelect')
            if (e.keyCode === 13) {
                var existingValue = $(this).attr('existingValue')
                var newValue = existingValue.split(',')
                var existingDataSource = d.dataSource._data

                if (d.value().length > 0) {
                    newValue.push(d.value()[0])
                    d.setDataSource(existingDataSource)
                    d.value(newValue)
                }
            } else {
                $(this).attr('existingValue', d.value())
            }
        })
    }
}
