export default function ModuleTypeCast(toolkit) {
    toolkit.hasProperty = (o, prop) => o.hasOwnProperty(prop)

    toolkit.isNumber = (o) => toolkit.isNotNaN(o) && toolkit.isFinite(o)
    toolkit.isString = (o) => toolkit.typeIs(o, 'string')
    toolkit.isNull = (o) => (o === null)
    toolkit.isDefined = (o) => !toolkit.isUndefined(o)
    toolkit.isUndefined = (o) => toolkit.typeIs(o, 'undefined')

    toolkit.isNaN = (o) => isNaN(o)
    toolkit.isNotNaN = (o) => !toolkit.isNaN(o)

    toolkit.isFinite = (o) => isFinite(o)
    toolkit.isInfinity = (o) => !toolkit.isFinite(o)

    toolkit.isEmptyString = (str, trim = false) => {
        if (trim) {
            return (toolkit.trim(str) === '')
        }

        return (str === '')
    }

    toolkit.isVoid = (o) => {
        return (
            toolkit.isUndefined(o)   ||
            toolkit.isNull(o)        ||
            toolkit.isNaN(o)         ||
            toolkit.isInfinity(o)    ||
            toolkit.isEmptyString(o)
        )
    }

    toolkit.isFunction = (o) => ((new Object()).toString.call(o) === `[object Function]`)
    toolkit.isArray = (o) => ((new Object()).toString.call(o) === `[object Array]`)
    toolkit.isObject = (o) => ((new Object()).toString.call(o) === `[object Object]`)

    toolkit.whenNull = (o, defaultValue) => toolkit.isNull(o) ? defaultValue : o
    toolkit.whenUndefined = (o, defaultValue) => toolkit.isUndefined(o) ? defaultValue : o
    toolkit.whenNaN = (o, defaultValue) => toolkit.isNaN(o) ? defaultValue : o
    toolkit.whenInfinity = (o, defaultValue) => toolkit.isInfinity(o) ? defaultValue : o
    toolkit.whenEmptyString = (o, defaultValue) => toolkit.isEmptyString(o) ? defaultValue : o
    toolkit.whenVoid = (o, defaultValue) => toolkit.isVoid(o) ? defaultValue : o

    toolkit.toInt = (o, base = 10) => parseInt(o, base)
    toolkit.toFloat = (o) => parseFloat(o)

    toolkit.redefine = (o, defaultValue, strict = true) => {
        let ok = toolkit.isUndefined(o)
        if (!strict) {
            ok = toolkit.isVoid(o)
        }

        return ok ? defaultValue : o
    }

    // THIS FUNCTION STILL NOT 100% FINISHED
    toolkit.typeIs = (target, comparator, mode = 'typeof') => {
        // if (toolkit.isString(comparator)) {
        //     if (['array', 'object', 'function'])

        // }
        if (mode === 'typeof') {
            return (typeof target === comparator)
        } else if (mode === 'instanceof') {
            return target instanceof comparator
        }
        
        toolkit.error(`unrecognized mode ${mode}`)
        return false
    }

    toolkit.number = (o, defaultValue = 0) => {
        if (toolkit.isString(o)) {
            o = toolkit.getNumberFromString(o)
        }
        
        return toolkit.isNumber(o) ? o : defaultValue
    }

    toolkit.string = (o) => {
        if (toolkit.isString(o)) {
            return o
        }
        
        return String(o)
    }
}
